<template>
    <div class="list" :key="tableData.length">
      <div class="user">
      {{cookieVal}}
      </div>
      <span style="font-size:20px;color:pink">总支出：{{this.sumMoney}}&nbsp;&nbsp;元</span>
    <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
        fixed
        prop="id"
        label="ID"
        width="125">
        </el-table-column>
        <el-table-column
        prop="name"
        label="支出项目"
        width="200">
        </el-table-column>
        <el-table-column
        prop="type"
        label="支出类型"
        width="125">
        </el-table-column>
        <el-table-column
        prop="money"
        label="支出金额"
        width="150">
        </el-table-column>
        <el-table-column
        prop="date"
        label="支出日期"
        width="200">
        </el-table-column>
        <el-table-column
        fixed="right"
        label="操作"
        width="125">
        <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small"><i class="el-icon-delete"></i>删除</el-button>
            <el-button @click="handleEdit(scope.row)" type="text" size="small"><i class="el-icon-edit"></i>编辑</el-button>
        </template>
        </el-table-column>
    </el-table>
    </div>
</template>

<script>
export default {
  methods: {
    getCookie (key) {
      var cookie = document.cookie
      var val = cookie.split('=')
      return val[1]
    },
    fetch () {
      this.$http.post('/list', '').then(res => {
        function getDate (timestamp) {
          var date = new Date(timestamp)
          const Y = String(date.getFullYear()) + '-'
          const M = String(date.getMonth() + 1) + '-'
          const D = String(date.getDate())
          return Y + M + D
        };
        if (res.data.state === '00') {
          this.$message.error('未登录，请先登录！')
          this.$router.push({ name: 'index' })
        } else {
          var dataList = res.data
          for (var i = 0; i < dataList.length; i++) {
            var obj = { real_id: dataList[i]._id, id: String(i + 1), name: dataList[i].itemName, type: dataList[i].itemType, money: dataList[i].itemPay + '元', date: getDate(dataList[i].itemDate) }
            this.tableData.push(obj)
            this.sumMoney = this.sumMoney + Number(dataList[i].itemPay)
          }
          console.log('总价格：', this.sumMoney)
        }
      })
    },
    handleClick (row) {
      var ID = { id: row.real_id }
      this.$http.post('/delete', ID).then(res => {
        console.log(res.data)
        this.$message({
          message: '删除成功',
          type: 'success'
        })
        console.log('删除后的List')
        console.log('------------')
        this.tableData = []
        this.fetch()
        console.log('------------')
      })
    },
    handleEdit (row) {
      this.$router.push({ name: 'edit', params: { id: row.real_id } })
    }
  },

  data () {
    return {
      tableData: [],
      cookieVal: '',
      sumMoney: 0
    }
  },
  created () {
    this.fetch()
    this.cookieVal = this.getCookie('username')
    if (!this.cookieVal) {
      this.$message.error('未登录，请先登录！')
      this.$router.push({ name: 'index' })
    }
  }
}
</script>
<style lang="scss" scoped>
    .list{
        position: relative;
        top: 20%;
        left: 10%;
        width: 80%;
        margin-top: 20px;
    }
    .user{
      width: 75px;
      height: 50px;
      border: 2px solid blue;
      text-align: center;
      line-height: 50px;
      border-radius: 50%;
    }
</style>
