<template>
<div>
    <div class="user">
        {{cookieVal}}
    </div>
    <div class="create">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="支出项目" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="支出类型" prop="type" size="medium" style="width:100%">
            <el-select v-model="ruleForm.type" placeholder="请选择类型" style="width:100%">
            <el-option label="衣" value="衣"></el-option>
            <el-option label="食" value="食"></el-option>
            <el-option label="住" value="住"></el-option>
            <el-option label="行" value="行"></el-option>
            <el-option label="其他" value="其他"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="支出金额" prop="money">
            <el-input v-model="ruleForm.money"></el-input>
        </el-form-item>
        <el-form-item label="支出日期" required >
            <el-col :span="11">
            <el-form-item prop="date" >
                <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.date" style="width: 100%;"></el-date-picker>
            </el-form-item>
            </el-col>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
        </el-form>
    </div>
</div>
</template>
<script>
export default {
  data () {
    return {
      ruleForm: {
        name: '',
        type: '',
        money: '',
        date: '',
        cookieVal: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入支出项目', trigger: 'blur' },
          { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '请选择支出类型', trigger: 'change' }
        ],
        money: [
          { required: true, message: '请输入支出金额', trigger: 'blur' },
          { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
        ],
        date: [
          { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    getCookie (key) {
      var cookie = document.cookie
      var val = cookie.split('=')
      return val[1]
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http.post('/create', this.ruleForm).then(res => {
            if (res.data.state === '00') {
              this.$message.error('未登录，请先登录！')
              this.$router.push({ name: 'index' })
            } else {
              this.$message({
                message: '账单创建成功',
                type: 'success'
              })
              this.$router.push({ name: 'list' })
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  },
  created () {
    this.cookieVal = this.getCookie('username')
    if (!this.cookieVal) {
      this.$message.error('未登录，请先登录！')
      this.$router.push({ name: 'index' })
    }
  }
}
</script>
<style lang="scss" scoped>
    .create{
        position: relative;
        top: 20%;
        left: 20%;
        width: 60%;
        margin-top: 50px;
    }
    .user{
      width: 75px;
      height: 50px;
      border: 2px solid blue;
      text-align: center;
      line-height: 50px;
      border-radius: 50%;
      margin-left: 20rem;
    }
</style>
